
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.content{
  height: 100vh;
  background-image: url("./images/pathway-middle-green-leafed-trees-with-sun-shining-through-branches.jpg");
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  display: grid;
  width: 100vw;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 2fr 1fr;
}


.console{
  grid-column: 2;
  grid-row: 1;
  display: flex;
  justify-content: space-between;
  align-items: end;
}






.gameboard .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 2px solid black;
  width: 2rem;
  height: 2rem;
  background-color: rgba(195, 195, 174, .6);
  color: #052622;
}

.gameboard{
  /* width: 16rem;
  height: 16rem; */
  display: grid;
  grid-template-columns: repeat(8,1fr);
  grid-template-rows: repeat(8,1fr);
  justify-self: end;
  margin-right: 50px;
}
.gameboard2{
  width: 18rem;
  height: 18rem;
  display: grid;
  grid-template-columns: repeat(12,1fr);
  grid-template-rows: repeat(12,1fr);
  justify-self: center;
  /* margin-right: 50px; */
  /* align-items: center; */
}

.gameboard2 .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 1px solid black;
  /* width: 1rem;
  height: 1rem; */
  background-color: rgba(195, 195, 174, .6);
  color: #052622;
}
.gameboard3{
  width: 18rem;
  height: 18rem;
  display: grid;
  grid-template-columns: repeat(16,1fr);
  grid-template-rows: repeat(16,1fr);
  justify-self: center;
  /* margin-right: 50px; */
  /* align-items: center; */
  background-image: url("./images/IMG_4723.JPG");
  background-position: 50%;
  background-size: contain;
  background-repeat: no-repeat;
}

.gameboard3 .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 1px solid black;
  /* width: 1rem;
  height: 1rem; */
  /* background-color: rgba(195, 195, 174, .6); */
  /* color: #052622; */
  color: white;
  background-color: rgb(0, 0, 0,.3);
  
}

.gameboard3 .hidden {
  background-color: rgba(160, 181, 138, 1);

}

.sprite,
.crying,
.sweating,
.winning{
  height: 300px;
  width: 300px;
  background: cover;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 2.5rem;
}
.sprite{
  background-image: url("../src/images/sprite2.gif");
}
.crying{
  background-image: url("../src/images/crying.gif");
  background: cover;
}
.sweating{
  background-image: url("../src/images/sweating.gif");
}
.winning{
  background-image: url("../src/images/winning.gif");
}

.board{
  display:grid;
  /* grid-template-columns: 1fr 5fr; */
 
}

/* footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
} */

.start-message{
  /* position: absolute; */
  display: grid;
  /* position: absolute; */
  justify-content: center;
  align-items: center;
  padding-top: 60px;
  /* margin-top: 40px; */
  /* margin: 5vw; */
  /* margin-right: 5vw; */
  width:  60vw;
  /* height: auto; */
  /* padding:5vw; */
  grid-column: 1;
  grid-row: 1 / 3;
  align-content: center;
}
.start-message>div,.start-message>h2{
  background-color:rgba(255, 255, 255,.7);
  padding: 5vw;
 
}

.App-header{
  background-color: #d4bf88;
  background-image: url("./images/wood-texture-close.jpg");
  background-size: cover;
  height: 2.5rem;
  border-bottom:2px solid #052622;
margin-top: -1.2rem;
  padding-top: .5rem;
  font-size: 1.5rem;
  font-family: 'Creepster', cursive;
  /* font-size: 80px; */
  color: white;
  font-weight: bold;
  /* margin-left: -1rem; */
  /* padding: 10px; */

  mix-blend-mode: multiply;
  text-shadow: 5px 4px 11px rgb(0,0,0), 0 2px 5px rgb(0,0,0);
  box-shadow: inset 4px 4px 10px #242424;
  /* text-align: center; */

}
header>img{
  margin-right: 5px;
}
.display{
  display: flex;
}


.counter,.message,button{
  background-color: rgba(255, 255, 255, .8);
  width: 4.5rem;
  height: 2.8rem;
  padding-top: 4px;
  margin-left: 5px;
  border-radius: 3px;
  border: 1px solid black;
  margin-bottom: 3px;
  text-align: center;
  /* margin-top: -1.9rem; */
}

button{
  height: 3.2rem;
}

.message{
  width: 6rem;
}

button{
  /* margin-left: 70vw; */

}

.main{
  grid-column: 2;
  grid-row: 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 5fr 1fr;
  align-items: start;
  align-items: end;
}

footer{
  background-color:white;
  display: flex;
  justify-content: space-around;
 
  align-items: center;
  font-size: clamp(1rem);
  font-family: 'Creepster', cursive;
  color: rgb(61, 61, 61);
  position:relative;
  /* margin-top: -10rem; */
  width: 100%;
  height: 180px;
  z-index: 1;
  border-top: 2px solid #052622;
  box-shadow: inset 0 0 10px #242424;
}


.gameboard .hidden,
.gameboard2 .hidden{
  background-color: rgba(160, 181, 138, .9);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 600px) {
  html, body {width: auto!important; overflow-x: hidden!important} 

  .start-message{
    width: 90vw;
    margin-left: 5vw;
  }

  .main{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .counter,.message{ 
    /* margin-top: -1.9rem; */
  }

  .main{
    align-items: flex-start;
  }

  .gameboard{
    justify-self: center;
    width: 80vw;
    height: 80vw;
  }
  
  .gameboard .space{
    width: 10vw;
    height: 10vw;
  }

  .gameboard2,.gameboard3{
    /* justify-self: start; */
    width: 96vw;
    height: 96vw;
  }
  /* .gameboard2 .space{
    width:6vw;
    height: 6vw;
  } */
  .board{
    margin-left: 2vw;
    /* grid-template-columns: 1fr;
    grid-template-rows: 1fr 10fr; */
    /* height: 96vw; */
  }
  .content{
    /* margin: 2vw -.5rem; */
    display: flex;
    flex-direction: column;
    /* grid-template-columns: 1fr;
    grid-template-rows: 1fr 10fr; */
    background-position: 50% 20%;
    height: 200vw;
    /* background-size: 300vh; */
  }

  .display{
    /* margin-top: -2rem; */
    /* display: grid;
    grid-template-columns: 1fr 2fr 1fr; */
    pointer-events: none;
  }

  .console{
    width: 96vw;
    margin-left: 2vw;
  }

  .go-back{
    margin-left: 50vw;
  }

  .crying,
  .sprite,
  .sweating,
  .winning{
    margin-top: .5rem;
    pointer-events: none;
    width: 80vw;
    height: 80vw;
    z-index: 1;
    /* align-self: start; */
  }

  .nav-button{
    /* display: relative; */
    margin-top: 5px;
  }

  Footer{
   position: relative;
   height: min(22vw,20vh);
   margin-top: min(-22vw,-10vh);
  }

}

@media screen and (orientation:landscape) and (max-width: 800px){
  Footer{
    margin-top: 0;
  }
  .content{ 
    height: 120vh;
  }
}
@media screen and (min-width: 1000px){
  /* .main{
    margin-top: 20vh;
  } */
  .nav-button{
    position:relative;
    margin-top: 20vh;
  }
  .start-message{
    margin-top: -10vh;
  }
  footer{
    height: 250px;
